.box{
  border:1px solid black;
  width: 500px;
  height: 550px;
  margin-right: 10px;
}

.item-img{
  display: block; /* 이미지를 블록 요소로 설정하여 수평 가운데 정렬을 용이하게 함 */
  margin: 0 auto;
  width:400px;
  height: 400px;
  
}

.main{
  display: flex;
  justify-content: center; /* 수평 가운데 정렬 */
  align-items: center; /* 수직 가운데 정렬 */
  margin: 10px;
}

.mainbox{
  display: flex;
  justify-content: center; /* 수평 가운데 정렬 */
  align-items: center; /* 수직 가운데 정렬 */
  margin-top: 100px;
}

.title{
  text-align: center;
}

.button{
  width: 50px;
  height: 40px;
  margin: 5px;
}

.Win{
  border:5px solid green;
  width: 500px;
  height: 550px;
  margin-right: 10px;
  background-color: yellowgreen;
}
.Lose{
  border:5px solid red;
  width: 500px;
  height: 550px;
  margin-right: 10px;
  background-color: gray;
}
.Tie{
  border:5px solid black;
  width: 500px;
  height: 550px;
  margin-right: 10px;
}